* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Montserrat; }

a, a:hover {
  text-decoration: none; }

a:-webkit-any-link:focus {
  outline: none; }

ul {
  list-style: none; }

button {
  cursor: pointer;
  outline: none; }

input, textarea {
  outline: none; }

@media (min-width: 576px) {
  .container {
    max-width: 540px; } }

@media (min-width: 768px) {
  .container {
    max-width: 720px; } }

@media (min-width: 1770px) {
  .container {
    max-width: 1570px; } }

@media (min-width: 2000px) {
  .container {
    max-width: 1770px; } }

.container {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  vertical-align: middle; }

img {
  vertical-align: middle;
  border-style: none;
  page-break-inside: avoid; }

.slick-arrow {
