.section {
  padding: 100px 0; }
  .section .block {
    width: 100%;
    margin-bottom: 40px; }
    .section .block__name {
      font-weight: 700;
      font-size: 50px;
      line-height: 61px;
      text-transform: uppercase;
      color: #3F5D65; }
  .section .share {
    display: flex;
    justify-content: space-between; }
    .section .share__info {
      max-width: 515px; }
    .section .share__text {
