          font-size: 18px;
          color: #3F5D65; }
        .section .share__slider .card__price {
          font-weight: 500;
          font-size: 20px;
          color: #EE5C5D;
          margin: 18px 0 28px; }
          .section .share__slider .card__price .price-old {
            margin-left: 21px;
