          .section .share__slider .card__info .info-name {
            font-weight: 400;
            font-size: 14px;
            color: #89A2A9;
            margin-right: 18px; }
            .section .share__slider .card__info .info-name p:nth-child(n+1) {
              margin-bottom: 15px; }
          .section .share__slider .card__info .info-meaning {
            font-weight: 500;
            font-size: 14px;
            color: #3F5D65; }
            .section .share__slider .card__info .info-meaning p:nth-child(n+1) {
              margin-bottom: 15px; }
            .section .share__slider .card__info .info-meaning .arrow-down::after {
