  .red-light .share .card:hover {
    background: #B1BFC3; }
  .red-light .share .card__name, .red-light .share .card__price,
  .red-light .share .card .info-name,
  .red-light .share .card .info-meaning {
    color: #fff; }
    .red-light .share .card__name .arrow-down::after, .red-light .share .card__price .arrow-down::after,
    .red-light .share .card .info-name .arrow-down::after,
    .red-light .share .card .info-meaning .arrow-down::after {
